<template>
  <v-layout
    wrap
    style="height:100vh;position:fixed; z-index:2; width:300px"
    fill-height
  >
    <v-layout justify-start>
      <v-btn
        style="height:40px;min-width:40px; margin:0"
        dark
        @click.stop="drawerNav"
        class="toggle"
      >
        <i class="fas fa-bars"></i>
      </v-btn>
    </v-layout>

    <v-navigation-drawer
      v-model="drawer"
      v-if="drawer"
      :mini-variant="mini"
      absolute
      dark
    >
      <v-list class="pa-1">
        <v-list-item avatar tag="div">
          <v-list-item-avatar>
            <img src="../../assets/avatar.jpg" />
          </v-list-item-avatar>

          <v-list-item-content>
            <v-list-item-title
              ><a href="/cv" style="text-decoration:none; color:white"
                >Vo Duc Dan</a
              ></v-list-item-title
            >
          </v-list-item-content>
        </v-list-item>
      </v-list>

      <v-list class="pt-0" dense>
        <v-divider light></v-divider>

        <v-list-item v-for="item in items" :key="item.title">
          <v-list-item-action>
            <span v-html="item.icon"></span>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>
              <router-link :to="item.path" class="navitem">{{
                item.title
              }}</router-link>
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
  </v-layout>
</template>
<script>
export default {
  data() {
    return {
      drawer: null,
      items: [
        {
          title: "About",
          path: "/Resume#about",
          icon: '<i class="fas fa-user"></i>'
        },
        {
          title: "Skill",
          path: "/Resume#skill",
          icon: '<i class="fas fa-signal"></i>'
        },
        {
          title: "Projects",
          path: "/Resume#projects",
          icon: '<i class="fas fa-chart-bar"></i>'
        },
        {
          title: "Education",
          path: "/Resume#education",
          icon: '<i class="fas fa-book"></i>'
        }
      ],
      mini: false,
      right: null
    };
  },
  methods: {
    drawerNav() {
      this.drawer = !this.drawer;
      const toggle = document.querySelector(".toggle");
      if (this.drawer === true) {
        toggle.classList.remove("unDrawer");
        toggle.classList.add("drawer");
      } else {
        toggle.classList.remove("drawer");
        toggle.classList.add("unDrawer");
      }
    }
  }
};
</script>
<style lang="scss">
.about-me {
  height: 100px !important;
}
.drawer {
  animation: drawer 0s forwards;
}
.unDrawer {
  animation: undrawer 0s forwards;
}
@-webkit-keyframes drawer {
  from {
    transform: translate(0);
  }
  to {
    transform: translate(250px, 0);
  }
}
@-webkit-keyframes undrawer {
  from {
    transform: translate(250px, 0);
  }
  to {
    transform: translate(0);
  }
}
.navitem {
  color: white;
  text-decoration: none;
  font-weight: 500;
}
</style>
